import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-parking-management-allotted',
  templateUrl: './parking-management-allotted.component.html',
  styleUrls: ['./parking-management-allotted.component.scss']
})
export class ParkingManagementAllottedComponent implements OnInit {

  apartmentBlockNumber:string="";
  blocksData:any;

  allSlotsData:any
  allottedSlotListData:any;

  isBlockSelected:boolean = false;

  isDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemUserStartIndex:any = 0;
  ItemUserEndIndex:any;
  totalUserItems:any;
  itemUserLimit:any = 20;

  slotTypeData:any;
  vehicleTypeData:any;

  modalService:any;

  @ViewChild("viewVehicleRef", { static: false }) viewVehicleRef: TemplateRef<any>;


  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private userService: UserService,
    private parkingService: ParkingService,
    private apartmentService: ApartmentService,
  	private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getUserIndexParams(event){
    this.ItemUserStartIndex = event.ItemUserStartIndex;
    this.ItemUserEndIndex = event.ItemUserEndIndex;
  }

  getSlotType(slotId, index){

    var data = _.filter( this.slotTypeData, item => {
      if(item.lookupValueId === slotId)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.allSlotsData[index].slotType = data[0].lookupValueName.toLowerCase();
      return data[0].lookupValueName.toLowerCase();
    }

    
  }

  showVehicleInfo(index) {

    var vehicleId = this.allSlotsData[index].vehicleId;

    this.vehicleService.getVehicleByVehicleId(vehicleId).subscribe((res : any) => {

      var vehicleData = res[0];

      var data = _.filter( this.vehicleTypeData, item => {
      
        if(item.lookupValueId === vehicleData.vehicleTypeId){
          return item;
        }

      });

      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        vehicleData.type = data[0].lookupValueName;
      }

      this.dialog.open(this.viewVehicleRef, {
        panelClass: 'material',
        disableClose: true,
        data: vehicleData
      });


    });

  }

  showSelectedBlockSlots(){
  	
    this.isBlockSelected = true;
    this.isDataLoaded = false;

    this.allottedSlotListData = [];

    _.each(this.allSlotsData, (item, index) => {

      var data = item.slotName.split(' ');
      var blockName = data[0];

      if(this.apartmentBlockNumber == blockName){
        this.allottedSlotListData.push(item);
      }

      this.totalUserItems = this.allottedSlotListData.length;

      if(this.totalUserItems>this.itemUserLimit){
        this.ItemUserEndIndex = this.itemUserLimit;
      }
      else {
        this.ItemUserEndIndex = this.totalUserItems;
      }

      this.vehicleService.getAllVehiclesByApartmentBlockUnitId(item.apartmentBlockUnitId).subscribe((data : any) => {

            if(data == undefined || data.length == 0){
              this.allSlotsData[index].isVehicleAvailable = false;
            }
            else {
              this.allSlotsData[index].isVehicleAvailable = true;
              this.allSlotsData[index].vehicleId = data[0].vehicleId;
            }

      });

      this.isDataLoaded = true;

    });

  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  ngOnInit() {

    let slotParams = {
      LookupTypeId: 18
    }

    //get slot type
    this.lookupService.getLookupValueByLookupTypeId(slotParams).subscribe((res:any) => {

      this.slotTypeData = res;

    },
    error => {
      
    }
    );

    let vehicleParams = {
      LookupTypeId: 2
    }

    //get vehicle type
    this.lookupService.getLookupValueByLookupTypeId(vehicleParams).subscribe((res:any) => {

      this.vehicleTypeData = res;

    },
    error => {
      
    }
    );

    let allSlotParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.vehicleService.getParkingSlotByApartmentId(allSlotParams).subscribe((res:any) => {

      this.allSlotsData = res.filter(item => {
        return item.isActive;
      });

      _.each(this.allSlotsData, (item, index) => {

        this.apartmentService.getApartmentBlockUnitById(item.apartmentBlockUnitId).subscribe((res:any) => {

          this.allSlotsData[index].blockNo = res[0].apartmentBlockNumber;
          this.allSlotsData[index].unitNo = res[0].apartmentBlockUnitNumber;

          this.apartmentService.getAllApartmentBlockUnitUsersByApartmentBlockUnitId(item.apartmentBlockUnitId).subscribe((res : any) => {

            if(res == undefined || res.length == 0){
              return '';
            }
            else {

              var userId = res[0].userId;

              this.userService.getUserById(userId).subscribe((res:any) => {

                 this.allSlotsData[index].userName = res[0].firstName + ' ' + res[0].lastName;;

              });

            }

          });

          

        });




      });

    },
    error => {
      
    }
    );

    let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res : any) => {

      this.blocksData = res;

    },
    error => {
      console.log(error);
    }
    );

	}

}
