import { Component, OnInit, Injector, HostListener } from '@angular/core';

import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-parking-management-settings',
  templateUrl: './parking-management-settings.component.html',
  styleUrls: ['./parking-management-settings.component.scss']
})
export class ParkingManagementSettingsComponent implements OnInit {


  isVehicleDataLoaded: boolean = false;
  vehicleCategoryData: any;

  isSlotMainDataLoaded: boolean = false;
  slotMainCategoryData: any;

  isSlotDataLoaded: boolean = false;
  slotCategoryData: any;
  modalService: any;

  constructor(
    private injector: Injector,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
  }


  ngOnInit() {
    this.getVehicleType();
    this.getMainSlotType();
    this.getSlotType();

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe(item => {
      if (item != null) {
        
        var params = {
          lookupValueId: item.lookupValueId,
          updateUserId: parseInt(this.cookieService.get('userId'))
        }
        this.lookupService.deleteLookupvalue(params).subscribe((res: any) => {
          // if (res.message) {
            this.refreshList({type:item.typeId});
            this.sharedService.setUnitListDeleteIndex(null);
          // }
        });


      }

    });

  }

  getVehicleType() {

    let categoryParams = {
      LookupTypeId: 2
    }

    //get vehcile type
    this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res: any) => {

      this.isVehicleDataLoaded = true;

      this.vehicleCategoryData = res.filter(item => {
        return item.isActive;
      });

    });
  }

  getMainSlotType() {

    let slotCategoryParams = {
      LookupTypeId: 71
    }

    //get main slot type
    this.lookupService.getLookupValueByLookupTypeId(slotCategoryParams).subscribe((res: any) => {

      this.isSlotMainDataLoaded = true;

      this.slotMainCategoryData = res.filter(item => {
        return item.isActive;
      });

    });
  }

  getSlotType() {

    let slotCategoryParams = {
      LookupTypeId: 18
    }

    //get slot type
    this.lookupService.getLookupValueByLookupTypeId(slotCategoryParams).subscribe((res: any) => {

      this.isSlotDataLoaded = true;

      this.slotCategoryData = res.filter(item => {
        return item.isActive;
      });

    });
  }

  deleteType(item, typeId) {
    item['typeId'] = typeId;
    this.modalService.showConfirmModal(item);
  }

  refreshList(e) {
    if (e.type == 2) {
      this.getVehicleType();
    } else if (e.type == 71) {
      this.getMainSlotType()
    } else {
      this.getSlotType();
    }
  }

}
