import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-parking-add-setup-vehicle-type',
  templateUrl: './parking-add-setup-vehicle-type.component.html',
  styleUrls: ['./parking-add-setup-vehicle-type.component.scss']
})
export class ParkingAddSetupVehicleTypeComponent implements OnInit {


  vehicleCategory: string = "";
  @Input() config: any;
  @Input() typeId: any;
  @Input() typeData: any;
  @Output() toggleDialog = new EventEmitter();
  @Output() emitConfig = new EventEmitter();


  constructor(
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService
  ) { }

  ngOnInit() {
    if (this.typeData && this.typeData.valueId) {
      this.vehicleCategory = this.typeData.valueName
    }
  }

  submitAddVehicleForm() {
    if (this.config.category != 'edit') {
      let details = {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "lookupTypeId": this.typeId,
        "lookupValueName": this.vehicleCategory,
        "description": this.vehicleCategory,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": "2019-11-20T16:51:24.105Z"
      }

      let params = {
        lookupvalue: details
      }

      this.lookupService.addLookupValue(params).subscribe((res: any) => {
        if (res.message) {
          this.sharedService.setAlertMessage(`${this.config.type} added successfully`);
          this.emitConfig.emit({isAdd:this.config.category,type:this.typeId});
        }
        else {
        }
      });
    }
    else {
      let details = {
        "lookupValueId": this.typeData.valueId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "lookupTypeId": this.typeId,
        "lookupValueName": this.vehicleCategory,
        "description": this.vehicleCategory,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-11-20T16:51:24.105Z",
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()
      }

      let params = {
        lookupvalue: details
      }

      this.lookupService.updateLookupValue(params).subscribe((res: any) => {
        if (res.message) {
          this.sharedService.setAlertMessage("Vehicle Type added successfully");
          this.emitConfig.emit({isAdd:this.config.category,type:this.typeId});
        }
        else {

        }

      });

    }

  }
  closeDialog(category) {
    this.toggleDialog.emit(category);
  }
}
