import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, filter, map, switchMap} from 'rxjs/operators';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { CookieService } from 'ngx-cookie-service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import * as moment from 'moment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

@Component({
  selector: 'app-parking-create-parking-booking',
  templateUrl: './parking-create-parking-booking.component.html',
  styleUrls: ['./parking-create-parking-booking.component.scss']
})
export class ParkingCreateParkingBookingComponent implements OnInit {
  slotSubject = new Subject<string>();
  slotData$:any;
  parkingSlotData:any = {};
  parkingSlotList:any;
  slotRateList = [];
  statusList = [];
  towerList = [];
  unitList = [];
  blockId:any;
  type:string;
  constructor( private parkingService: ParkingService, private cookieService:CookieService,
    private sharedService: SharedService,private router : Router,
    private lookupService : LookupService,
    private apartmentService:ApartmentService,
    private activateRouter: ActivatedRoute) { }

  onSelectSlot(event) {
    let selectedValue = event.option.value;
    this.parkingSlotData.displaySlotNumber =  `${selectedValue.parkingSlotNo} location ${selectedValue.slotTypeName}(${selectedValue.permOwner} ${selectedValue.apartmentBlockNumber} ${selectedValue.apartmentBlockUnitNumber})`;
    this.parkingSlotData.parkingSlotNo = selectedValue.parkingSlotNo;
    this.parkingSlotData.parkingSlotID = selectedValue.parkingSlotID;
    this.parkingSlotData.apartmentBlockUnitNumber = selectedValue.apartmentBlockUnitNumber;
    this.parkingSlotData.permOwner = selectedValue.permOwner;
    this.parkingSlotData.slotTypeId =  selectedValue.slotTypeId;
    this.parkingSlotData.apartmentBlockNumber =  selectedValue.apartmentBlockNumber
  }

  submitParkingSlot() {
    let entity = {
      "id": 0,
      "startDate": this.parkingSlotData.startDate,
      "endDate":  this.parkingSlotData.endDate,
      "startTime": moment(this.parkingSlotData.startTime).format('HH:mm:ss'),
      "endTime": moment(this.parkingSlotData.endTime).format('HH:mm:ss'),
      "availabelParkingId": 0,
      "rentedParkingAllocationTypeId": 0,
      "totalFee": 0,
      "isCancelled": true,
      "refundAmount": 0,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null,
      "visitorId": 0,
      "apartmentBlockUnitId": 0,
      "currentParkingTrackerId": 0,
      "addToMonthyBill": true
    }
    let parking = {
      allocParking : entity
    }
    this.parkingService.allocateParking(parking).subscribe((res:any)=>{
      if(!isNaN(res.message)) {
        this.sharedService.setAlertMessage("Parking Slot Created successfully");
        this.router.navigate(['/ams/parking-management/set-parking-availablity/unit-parking-availablity'])
      } else if(res.message) {
        this.sharedService.setAlertMessage(res.message);
      } else {
        this.sharedService.setAlertMessage(res.errorMessage);
      }
    })
  }

  getUnits(type) {
    let unitParams = {
      apartmentBlockId: this.blockId
    }
    this.apartmentService.getApartmentBlockUnitByBlockId(unitParams).subscribe((res:any) => {
      this.unitList = res;
    },
    error => {
      console.log(error);
    });
  }


  ngOnInit() {
    this.activateRouter.params.subscribe(param=>this.type = param['type']);
    this.slotData$ = this.slotSubject.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      filter(data=>data.length > 2),
      switchMap(term=>{
        let entity = {
          ApartmentID: parseInt(this.cookieService.get('apartmentId')),
          KeywordSlotNo: term,
          SlotTypeID : this.type == 'unit' ? 299 : 300,
        }
        return this.parkingService.getParkingSlotNoByKeyword(entity)
    }))
    this.slotData$.subscribe(res=>{
      if(res.length > 0) {
        this.parkingSlotList = res;
      }
    })

    //Slot Rate Type List
    let getParam = {
      ApartmantId:parseInt(this.cookieService.get('apartmentId'))
    }
    this.parkingService.getParkingAllocationType(getParam).subscribe((resp:any) =>{
      this.slotRateList = resp.filter(data => {
        return data.isActive
      });
    },error =>{
      console.log(error)
    })

    //Status
    let slotTypeParams = {
      LookupTypeId: 97
    }
    this.lookupService.getLookupValueByLookupTypeId(slotTypeParams).subscribe((res: any) => {
      this.statusList = res.filter(item => {
        return item.isActive
      });
    })
    //tower
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      this.towerList = res;
    });
  }
}
