import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';

@Component({
  selector: 'app-parking-aa-unit-to-community',
  templateUrl: './parking-aa-unit-to-community.component.html',
  styleUrls: ['./parking-aa-unit-to-community.component.scss']
})
export class ParkingAaUnitToCommunityComponent implements OnInit {

  unitAllocationHeader:any
  unitAllocationData:any;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(private parkingService : ParkingService, private cookieService:CookieService) { }

  getUnitAvailability() {
    let params = {
      ApartmantId: parseInt(this.cookieService.get('apartmentId')),
      ApartmentBlockId: null,
      ParkingSlotId: null,
      StartDate: null,
      EndDate: null,
      StartTime: null,
      EndTime: null,
      SlotMainTypeID:299 //UnitOwnedId
    };
    this.parkingService.getAllRentedParkingAllocation(params).subscribe((res:any)=>{
      this.unitAllocationData  = res;
      this.gridSourceData = {
        localdata: this.unitAllocationData,
        datatype: "array"
      }
      this.unitAllocationData = new jqx.dataAdapter(this.gridSourceData);
    })
  }

  ngOnInit() {
    this.getUnitAvailability();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.unitAllocationHeader = [{
      text: 'SLOT Name',
      datafield: 'slotname',
      width: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'SLOT Type',
      datafield: 'spelotTy',
      minwidth: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer   
     },{
      text: 'Perm.Owner Unit',
      datafield: 'tempBlock',
      minwidth: 140,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Perm.Owner Name',
      datafield: 'permOwner',
      minwidth: 140,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Temp.Owner Unit',
      datafield: 'permr',
      minwidth: 140,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Temp.Owner Name',
      datafield: 'per',
      minwidth: 140,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'START DATE',
      datafield: 'startDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'START TIME',
      datafield: 'startTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END DATE',
      datafield: 'endDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END TIME',
      datafield: 'endTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'SlotRate Type',
      cellsalign: 'status', 
      align: 'center',
      width: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Price',
      cellsalign: 'stat', 
      align: 'center',
      width: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
    }];
  }
}
