import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-add-slot',
  templateUrl: './add-slot.component.html',
  styleUrls: ['./add-slot.component.scss']
})
export class AddSlotComponent implements OnInit {
  
  @Input ('index') index;
  @Input ('array') slotArray;

  slot:any;
  slotType:string = null;

  blocksData:any;
  unitData:any;

  apartmentBlockId:string = "";
  apartmentBlockUnitId:string = "";

  isEditSlot:boolean = false;

  isSlotSelected:boolean = false;

  isSlotSubmitted:boolean = false;
  isSlotAdded:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  isBlockSelected:boolean = false;

  @Output() public outputParams = new EventEmitter();

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private vehicleService: VehicleService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService

  ) { }


  isArrayOne(){
  	return this.slotArray.length > 1 ? true : false;
  }

  deleteSlot(){
  	this.slotArray.splice(this.index, 1);
  	this.outputParams.emit(this.slotArray.length);
  }

  getUnits() {

    let blockParams = {
      apartmentBlockId: parseInt(this.slot.apartmentBlockId)
    }

    this.apartmentService.getApartmentBlockUnitByBlockId(blockParams).subscribe((res:any) => {
        this.isSlotSelected = true;
        this.isBlockSelected = true;
        this.unitData = res;
    },
    error => {
      console.log(error);
    }
    );
  }

  submitAddSlotForm(form) {

    this.isSlotSubmitted = true;

    if(!this.isEditSlot) {

      let details = {
      
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "apartmentBlockUnitId": parseInt(this.slot.apartmentBlockUnitId),
        "slotName": this.slot.slotName,
        "slotTypeId": parseInt(this.slotType),
        "comment": this.slot.comment,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-11-19T19:53:19.815Z",
        "updatedBy": 0,
        "updatedOn": "2019-11-19T19:53:19.815Z"

      }

      let params = {
        parkingSlot: details
      }

      this.vehicleService.addParkingSlot(params).subscribe((res:any) => {
          
         if(res.message) {
           this.isSlotAdded = true;
         }
         else {

            this.isSlotSubmitted = false;
            this.isSlotAdded = false;
            this.isError = true;
            this.errorMessage = res.errorMessage;

         }
 

      },
      error => {
        
      }
      );

  }

  else {

    let details = {

        "parkingSlotId":this.route.params['value'].id,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "apartmentBlockUnitId": parseInt(this.slot.apartmentBlockUnitId),
        "slotName": this.slot.slotName,
        "slotTypeId": parseInt(this.slotType),
        "comment": this.slot.comment,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-11-19T19:53:19.815Z",
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": "2019-11-19T19:53:19.815Z"

     }

     let params = {
      parkingSlot: details
    }

     this.vehicleService.updateParkingSlot(params).subscribe((res:any) => {
          
         if(res.message) {
           this.isSlotAdded = true;
         }
         else {

          this.isSlotSubmitted = false;
          this.isSlotAdded = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;

         }
 

    },
    error => {
      
    }
    );

  }

    

    

  }


  ngOnInit() {

  	this.slot = {};
    this.slotType = "";

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    //get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {

      this.blocksData = res;
      
    });

    if(this.route.params['value'].id != undefined) {
      
      this.isEditSlot = true;

      this.isBlockSelected = false;

      this.vehicleService.getParkingSlotById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.slot = res[0];
        this.slotType = this.slot.slotTypeId.toString();
        this.slot.apartmentBlockUnitId = res[0].apartmentBlockUnitId.toString();

        //get block unit
        this.apartmentService.getApartmentBlockUnitById(this.slot.apartmentBlockUnitId).subscribe((block:any) => {

          this.slot.apartmentBlockId = block[0].apartmentBlockId.toString();
          
          let params = {
            apartmentBlockId: parseInt(this.slot.apartmentBlockId)
          }

          this.apartmentService.getApartmentBlockUnitByBlockId(params).subscribe((res:any) => {
              this.isSlotSelected = true;
              this.unitData = res;
          },
          error => {
            console.log(error);
          }
          );


        },
        error => {

        });


      },
      error => {
        
      }
      );

      

    }

    


  }

}
