import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';

@Component({
  selector: 'app-unit-parking-availablity',
  templateUrl: './unit-parking-availablity.component.html',
  styleUrls: ['./unit-parking-availablity.component.scss']
})
export class UnitParkingAvailablityComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  unitAllocationHeader:any
  unitAllocationData:any;
  gridSourceData:any;
  unitFilter:string;
  isDataLoaded:boolean = true;

  constructor(private parkingService : ParkingService,
    private cookieService:CookieService,) { }

  unitSearch() {
    if(this.unitFilter != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.unitFilter;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.unitAllocationHeader.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  filterData(event) {
    console.log(event)
  }

  getUnitAvailability() {
    this.isDataLoaded = true;
    let params = {
      ApartmantId: parseInt(this.cookieService.get('apartmentId')),
      ApartmentBlockId: null,
      ParkingSlotId: null,
      StartDate: null,
      EndDate: null,
      StartTime: null,
      EndTime: null,
      SlotMainTypeID:299 //UnitOwnedId
    };
    this.parkingService.getAllRentedParking(params).subscribe((res:any)=>{
      this.unitAllocationData  = res;
      this.gridSourceData = {
        localdata: this.unitAllocationData,
        datatype: "array"
      }
      this.unitAllocationData = new jqx.dataAdapter(this.gridSourceData);
      this.isDataLoaded = false;
    })
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'UnitAvailability');
  }

  ngOnInit() {
    this.getUnitAvailability();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.unitAllocationHeader = [{
      text: 'SLOT NO',
      datafield: 'slotType',
      width: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'SLOT Location',
      datafield: 'parkingSlotLocation',
      minwidth: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer   
     },{
      text: 'Perm.Owner Name',
      datafield: 'permOwner',
      minwidth: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Perm.Owner Unit',
      datafield: 'k',
      minwidth: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'START DATE',
      datafield: 'startDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'START TIME',
      datafield: 'startTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END DATE',
      datafield: 'endDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END TIME',
      datafield: 'endTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'CREATED BY',
      cellsalign: 'status', 
      align: 'center',
      width: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
    }];
  }
}
