import { Component, OnInit, ViewChild, Injector, HostListener } from '@angular/core';
import { AllocatedSlotConfirmationComponent } from '../allocated-slot-confirmation/allocated-slot-confirmation.component';
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-parking-manage-allocated-slots',
  templateUrl: './parking-manage-allocated-slots.component.html',
  styleUrls: ['./parking-manage-allocated-slots.component.scss']
})
export class  ParkingManageAllocatedSlotsComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  allocatedSlotData:any;
  allocatedSlotHeader: any;
  allocateFilter:string;
  isDataLoaded:boolean = true;
  apiUnsubcribe:Subscription;
  public modalService:any;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private vehicleService: VehicleService,
    private sharedService:SharedService,
    private cookieService:CookieService,
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  @HostListener('window:ondeleteSlot', ['$event.detail'])
  ondeleteSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let parkingSlotId = dataRecord.parkingSlotId
    this.modalService.showConfirmModal(parkingSlotId);
  }

  @HostListener('window:oneditSlot', ['$event.detail'])
  oneditSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    const dialogRef = this.dialog.open(AllocatedSlotConfirmationComponent, {
      width: '600px',
      height:'auto',
      data: dataRecord
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.getAllocatedSlot();
      }
    });
  }
   
  searchAllocate() {
    if(this.allocateFilter != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.allocateFilter;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.allocatedSlotHeader.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getAllocatedSlot(){
    this.isDataLoaded = true;
    let getParam ={
      apartmentId:parseInt(this.cookieService.get('apartmentId'))
    }
    this.vehicleService.getParkingSlotByApartmentId(getParam).subscribe((res:any) => {
      var rawSlotRateType = res.filter(data => {
        return data.isActive
      });
      let arrangeSlotRawData = {
        localdata: rawSlotRateType,
        datatype: "array"
      }
      this.allocatedSlotData = new jqx.dataAdapter(arrangeSlotRawData);
      this.isDataLoaded = false;
    });
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'AllocatedSlot');
  }

  ngOnInit() {
    this.getAllocatedSlot();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.allocatedSlotHeader = [{
      text: 'slot name',
      datafield: 'slotName',
      cellsrenderer: cellsrenderer,
      minwidth:80,
      renderer: columnrenderer
    },{
      text: 'Slot Location',
      cellsalign: 'center',
      align: 'center',
      datafield: 'parkingSlotBlockNumber_Label',
      cellsrenderer: cellsrenderer,
      minwidth:80,
      renderer: columnrenderer
    },{
      text: 'owner unit',
      cellsalign: 'center',
      align: 'center',
      datafield: 'apartmentBlockUnitNumber',
      cellsrenderer: (row, column, unit) => {
        let block = this.allocatedSlotData.loadedData[row].apartmentBlockNumber;
        block = block == null ? '' : block;
        unit = unit == null ? '' : unit;
        return `<div class="jqx-custom-inner-cell">${block} ${unit}</div>`;
      },
      minwidth:80,
      renderer: columnrenderer
    },{
      text: 'slot main type',
      datafield: 'slotMainTypeName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'slot type',
      datafield: 'slotTypeName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'allocations',
      datafield: 'slotMainTypeId',
      cellsalign: 'center',
      align: 'center',
      cellsrenderer: (row, column, typeId) => {
        if((typeId == 299 && this.allocatedSlotData.loadedData[row].apartmentBlockUnitId) || (typeId == 300 && this.allocatedSlotData.loadedData[row].staffId)) {
          return '<div class="simple-actions"><span class="dots bg-green-900"></div>';
        } else {
          return '<div class="simple-actions"><span class="dots bg-red-900 link" onClick="editAllocateSlot(' + row + ')"></div>';
        }
      },
      minwidth: 80,
      renderer: columnrenderer
    }, {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editAllocateSlot(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDelete(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      minwidth: 100,
      renderer: columnrenderer
    }];
     //delete item
    this.apiUnsubcribe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
      if(item != null){
        var params = {
          parkingSlotId: item,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }
        this.vehicleService.deleteParkingSlot(params).subscribe((res:any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          this.getAllocatedSlot();
        });
      }
    });
  }

  ngOnDestroy() {
    this.apiUnsubcribe.unsubscribe();
  }
}

function showConfirmDelete(row){
  var event = new CustomEvent('ondeleteSlot', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDelete = showConfirmDelete;

function editAllocateSlot(row){
  var event = new CustomEvent('oneditSlot', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editAllocateSlot = editAllocateSlot;
