import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add-slot-container',
  templateUrl: './add-slot-container.component.html',
  styleUrls: ['./add-slot-container.component.scss']
})
export class AddSlotContainerComponent implements OnInit {

  slotArray:any;
  slotCount:number = 1;
  isEditSlot:boolean = false;


  constructor(
  	private router: Router,
    private route: ActivatedRoute
  ) { }

  addSlot() {
    this.slotCount++;
    this.slotArray =  Array(this.slotCount).fill(0).map((x,i)=>i);
  }

  getSlotArray(event){
    this.slotCount = event;
    this.slotArray = Array(this.slotCount).fill(0).map((x,i)=>i);
  }

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.isEditSlot = true;
    }

    this.slotArray =  Array(this.slotCount).fill(0).map((x,i)=>i);

  	

  }

  

}
