import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ParkingManagementComponent } from './parking-management.component';
import { ParkingManagementSettingsComponent } from './components/parking-management-settings/parking-management-settings.component';
import { ParkingManagementAllottedComponent } from './components/parking-management-allotted/parking-management-allotted.component';
import { ParkingManagementUnallottedComponent } from './components/parking-management-unallotted/parking-management-unallotted.component';
import { ParkingManagementReportsComponent } from './components/parking-management-reports/parking-management-reports.component';
import { AddSlotComponent } from './components/add-slot/add-slot.component';
import { AddSlotContainerComponent } from './components/add-slot-container/add-slot-container.component';
import { ParkingManageParkingSlotsComponent } from './components/parking-manage-parking-slots/parking-manage-parking-slots.component';
import { ParkingCreateParkingSlotComponent } from './components/parking-create-parking-slot/parking-create-parking-slot.component';
import { ParkingAllocateSlotsComponent } from './components/parking-allocate-slots/parking-allocate-slots.component';
import { ParkingManageAllocatedSlotsComponent } from './components/parking-manage-allocated-slots/parking-manage-allocated-slots.component';
import { ParkingManageAllocateAvailabilityListComponent } from './components/parking-manage-allocate-availability-list/parking-manage-allocate-availability-list.component';
import { ParkingCreateParkingBookingComponent } from './components/parking-create-parking-booking/parking-create-parking-booking.component';
import { ParkingViewAvailableSlotsComponent } from './components/parking-view-available-slots/parking-view-available-slots.component';
import { ParkingAaUnitToUnitAllocationComponent } from './components/parking-manage-allocate-availability-list/parking-aa-unit-to-unit-allocation/parking-aa-unit-to-unit-allocation.component';
import { ParkingAaUnitToCommunityComponent } from './components/parking-manage-allocate-availability-list/parking-aa-unit-to-community/parking-aa-unit-to-community.component';
import { ParkingAaCommunityToOthersComponent } from './components/parking-manage-allocate-availability-list/parking-aa-community-to-others/parking-aa-community-to-others.component';
import { ParkingManageBookingComponent } from './components/parking-manage-booking/parking-manage-booking.component';
import { ParkingManageBookingCommunityToOtherComponent } from './components/parking-manage-booking/parking-manage-booking-community-to-other/parking-manage-booking-community-to-other.component';
import { ParkingManageBookingUnitToCommunityComponent } from './components/parking-manage-booking/parking-manage-booking-unit-to-community/parking-manage-booking-unit-to-community.component';
import { ParkingAdminViewBookingComponent } from './components/parking-admin-view-booking/parking-admin-view-booking.component';
import { ParkingBookingViewCommunityOwnedToUnitComponent } from './components/parking-admin-view-booking/parking-booking-view-community-owned-to-unit/parking-booking-view-community-owned-to-unit.component';
import { ParkingBookingViewCommunityOwnedToVistorComponent } from './components/parking-admin-view-booking/parking-booking-view-community-owned-to-vistor/parking-booking-view-community-owned-to-vistor.component';
import { ParkingSetupComponent } from './components/parking-setup/parking-setup.component';
import { AddVehicleComponent } from './components/add-vehicle/add-vehicle.component';
import { ParkingAddSetupVehicleTypeComponent } from './components/parking-management-settings/parking-add-setup-vehicle-type/parking-add-setup-vehicle-type.component';
import { ParkingSlotRateComponent } from './components/parking-slot-rate/parking-slot-rate.component';
import { SetParkingAvailablityComponent } from './components/set-parking-availablity/set-parking-availablity.component';
import { UnitParkingAvailablityComponent } from './components/set-parking-availablity/unit-parking-availablity/unit-parking-availablity.component';
import { CommunityParkingAvailablityComponent } from './components/set-parking-availablity/community-parking-availablity/community-parking-availablity.component';
import { CreateAvailablityComponent } from './components/set-parking-availablity/create-availablity/create-availablity.component';


const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	// { path: 'settings', component: ParkingManagementSettingsComponent},
	{ path: 'setup', component: ParkingSetupComponent,
    children:[
		{ path: '', redirectTo: 'settings', pathMatch: 'full'},
		{ path: 'settings', component: ParkingManagementSettingsComponent},
		{ path: 'parking-slot-rate', component: ParkingSlotRateComponent},

	]},
	{ path: 'manage-parking-slots', component: ParkingManageParkingSlotsComponent},
	{ path: 'create-parking-slot', component: ParkingCreateParkingSlotComponent},
	{ path: 'edit-parking-slot/:id', component: ParkingCreateParkingSlotComponent},
	{ path: 'manage-allocated-slots', component: ParkingManageAllocatedSlotsComponent},
	{ path: 'allocate-slots', component: ParkingAllocateSlotsComponent},

	{ path: 'manage-allocate-availability-list', component: ParkingManageAllocateAvailabilityListComponent,
		children: [
			{ path: '', redirectTo: 'unit-to-unit-allocation', pathMatch: 'full'},
			{ path: 'unit-to-community', component: ParkingAaUnitToCommunityComponent},
			{ path: 'community-to-others', component: ParkingAaCommunityToOthersComponent}, 
			{ path: 'unit-to-unit-allocation', component: ParkingAaUnitToUnitAllocationComponent}, 
			{ path: '**', redirectTo: 'unit-to-unit-allocation', pathMatch: 'full'}
		]
	},
	{ path: 'set-parking-availablity', component: SetParkingAvailablityComponent,
		children: [
			{ path: '', redirectTo: 'unit-parking-availablity', pathMatch: 'full'},
			{ path: 'unit-parking-availablity', component: UnitParkingAvailablityComponent},
			{ path: 'community-parking-availablity', component: CommunityParkingAvailablityComponent}, 
			{ path: '**', redirectTo: 'unit-parking-availablity', pathMatch: 'full'},
		]
	},
	{ path:'create-availability/:type',component: CreateAvailablityComponent},
	{ path: 'manage-booking-slots', component: ParkingManageBookingComponent},
	
	{ path: 'view-booking', component: ParkingAdminViewBookingComponent},
	{ path: 'create-allocation/:type', component: ParkingCreateParkingBookingComponent},
	{ path: 'view-available-slots', component: ParkingViewAvailableSlotsComponent},
	{ path: 'manage-booking-slots', component: ParkingManageBookingComponent},
	{ path: 'allotted', component: ParkingManagementAllottedComponent},
	{ path: 'unallotted', component: ParkingManagementUnallottedComponent},
	{ path: 'reports', component: ParkingManagementReportsComponent},
	{ path: 'add-slot', component: AddSlotContainerComponent},
	{ path: 'edit-slot/:id', component: AddSlotContainerComponent },
	{ path: 'add-vehicle/:slotid', component: AddVehicleComponent},
	{ path: 'edit-vehicle/:id', component: AddVehicleComponent},
	{ path: 'edit-allotted-slot/:id', component: AddSlotContainerComponent },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ParkingManagementRoutingModule { }
