import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { ParkingService } from 'src/app/api/controllers/Parking';
import * as moment from 'moment';


@Component({
  selector: 'app-parking-admin-view-booking',
  templateUrl: './parking-admin-view-booking.component.html',
  styleUrls: ['./parking-admin-view-booking.component.scss']
})
export class ParkingAdminViewBookingComponent implements OnInit {

  public searchBookingHistory: any;

  constructor(private cookieService: CookieService, private parkingService: ParkingService) { }

  navArray: any;

  ngOnInit() {
    this.searchBookingHistory = {
      ApartmentID: this.cookieService.get('apartmentId'),
      StartDate: '',
      EndDate: '',
      StartTime: '',
      EndTime: ''
    }
  }

  getBookingHistoryList() {
    let searchHistoryParam = {
      ApartmentID: Number(this.cookieService.get('apartmentId')),
      StartDate: this.searchBookingHistory.StartDate,
      EndDate: this.searchBookingHistory.EndDate,
      StartTime: moment(this.searchBookingHistory.StartTime, 'hh:mm a').format(),
      EndTime: moment(this.searchBookingHistory.EndTime, 'hh:mm a').format()
    }
    this.parkingService.getParkingAllocationSummary(searchHistoryParam).subscribe(
      resp => {
        console.log('resp',resp);
      },
      error => {

      }
    )
  }

}
