import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'app-parking-manage-filter',
  templateUrl: './parking-manage-filter.component.html',
  styleUrls: ['./parking-manage-filter.component.scss']
})
export class ParkingManageFilterComponent implements OnInit {
  filter:any = {};
  @Input('filterType')filterType:any;
  @Output('filterObj') filterObj = new EventEmitter();
  
  constructor() { }

  filterSlot() {
    this.filterObj.emit(this.filter)
  }
  
  ngOnInit(): void {
  }

}
