import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-parking-allocate-slots',
  templateUrl: './parking-allocate-slots.component.html',
  styleUrls: ['./parking-allocate-slots.component.scss']
})
export class ParkingAllocateSlotsComponent implements OnInit {
  
  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  constructor() { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }
  
  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  ngOnInit() {
  }

}
