import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

@Component({
  selector: 'app-parking-create-parking-slot',
  templateUrl: './parking-create-parking-slot.component.html',
  styleUrls: ['./parking-create-parking-slot.component.scss']
})
export class ParkingCreateParkingSlotComponent implements OnInit {
  slotMainTypeList: Array<any> = [];
  slotTypeList: Array<any> = [];
  parkingTypeList: Array<any> = [];
  towerList: Array<any> = [];
  createSlot: any;
  slotId: any
  isCreateSlot: boolean = true;
  constructor(
    private parkingService : ParkingService,
    private vehicleService : VehicleService,
    private lookupService : LookupService,
    private router : Router,
    private apartmentService: ApartmentService,
    private cookieService: CookieService,
    private sharedService: SharedService,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.createSlot = {
      "parkingSlotId": 0,
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "apartmentBlockUnitId": null,
      "slotName": "",
      "slotTypeId": 0,
      "slotStatusId": null,
      "comment": "",
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null,
      "parkingTypeId": 0,
      "parkingSlotBlockNumber": 0,
      "sqrFt": 0,
      "slotMainTypeId": 0,
      "permSlotStatusId": null
    }
    this.getMasterData();
    this.slotId = this.activatedRoute.params['value'].id;
    if (this.slotId) {
      this.isCreateSlot = false;
      this.getParkingSlot();
    } else {
      this.isCreateSlot = true;
    }
  }

  getMasterData() {
    let mainTypeParam = {
      LookupTypeId: 71
    }
    //slot main type
    this.lookupService.getLookupValueByLookupTypeId(mainTypeParam).subscribe((res: any) => {
      this.slotMainTypeList = res.filter(item => {
        return item.isActive
      });
    })
     //slot type
    let slotTypeParams = {
      LookupTypeId: 18
    }
    this.lookupService.getLookupValueByLookupTypeId(slotTypeParams).subscribe((res: any) => {
      this.slotTypeList = res.filter(item => {
        return item.isActive
      });
    })
    //parking type
    this.parkingService.getParkingType().subscribe((resp: any) => {
      this.parkingTypeList = resp;
    });
    let towerParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.apartmentService.getApartmentBlockByApartmentId(towerParams).subscribe((res: any) => {
      this.towerList = res;
    });
  }

  getParkingSlot() {
    let params = {
      parkingSlotId: this.slotId,
    }
    this.vehicleService.getParkingSlotById(params).subscribe(resp => {
      this.createSlot = resp[0];
    })
  }

  selectSlotType() {
    if(this.createSlot.slotMainTypeId == 299) { //  Unit Slot Type is applicable for Unit Owned
      this.createSlot.slotTypeId = 81 // Unit Owned Id
    } else {
      this.slotTypeList.forEach((data,index)=>{
        if(data.lookupValueId == 81) {
          this.slotTypeList.splice(index,1);
        }
      })
    }
  }


  parkingSlot() {
    let entity = {
      parkingSlot : this.createSlot
    }
    if (this.isCreateSlot) {
      this.createSlot.sqrFt = parseFloat(this.createSlot.sqrFt);
      this.vehicleService.addParkingSlot(entity).subscribe(resp => {
        this.sharedService.setAlertMessage("Parking Slot Created successfully");
        this.router.navigate(['/ams/parking-management/manage-parking-slots'])
      })
    } else {
      this.vehicleService.updateParkingSlot(entity).subscribe(resp => {
        this.sharedService.setAlertMessage("Parking Slot Updated successfully");
        this.router.navigate(['/ams/parking-management/manage-parking-slots'])
      })
    }
  }
}
