import { Component, OnInit } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
	selector: 'app-parking-management-slot-types',
	templateUrl: './parking-management-slot-types.component.html',
	styleUrls: ['./parking-management-slot-types.component.scss']
})
export class ParkingManagementSlotTypesComponent implements OnInit {

	isDataLoaded: boolean = false;
	isSlotTypeSubmitted: boolean = true;

	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 10;

	unitFieldType: string = "unitno";
	unitOrder: boolean = true;

	isMobile: boolean = false;

	slotData: any;
	slotDataList: any;

	isSlotTypeNew: boolean = false;
	isSlotTypeUpdate: boolean = false;

	slotType: string = "";
	slotTypeUpdateId: number = 0;

	isError: boolean = false;
	alertMessage: boolean = false;

	slotMainTypeDataList: any;

	slot: any = {
		slotMainType: '',
		slotType: ''
	}

	constructor(
		private lookupService: LookupService,
		private sharedService: SharedService,
		private cookieService: CookieService
	) { }

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}

	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}

	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'asc' : 'desc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}

	addNewSlotType() {

		this.isSlotTypeNew = true;
		this.isSlotTypeUpdate = false;

		setTimeout(() => {

			var elem = document.querySelector('.slot-type-card');
			var scrollTo = elem.getBoundingClientRect().top - 100;

			window.scroll({
				top: scrollTo,
				behavior: 'smooth'
			});

		}, 100);

	}

	updateSlotType(item) {

		this.isSlotTypeUpdate = true;
		this.isSlotTypeNew = false;
		this.slot.slotMainType = item.lookupValueName;
		this.slot.slotType = item.description;
		this.slotTypeUpdateId = item.lookupValueId;

		setTimeout(() => {

			var elem = document.querySelector('.slot-type-card');
			var scrollTo = elem.getBoundingClientRect().top - 100;

			window.scroll({
				top: scrollTo,
				behavior: 'smooth'
			});

		}, 100);

	}

	deleteSlotType(item, index) {

		this.isDataLoaded = false;

		var params = {
			lookupValueId: item.lookupValueId,
			updateUserId: parseInt(this.cookieService.get('userId'))
		}

		this.lookupService.deleteLookupvalue(params).subscribe((res: any) => {
			this.getSlotType();
			// this.slotDataList.splice(index, 1);
			// this.isDataLoaded = true;

		});

	}

	removeSlotTypeBox() {

		this.isSlotTypeNew = false;
		this.isSlotTypeUpdate = false;
		this.isError = false;

		setTimeout(() => {

			var elem = document.querySelector('.slot-type-table-card');
			var scrollTo = elem.getBoundingClientRect().top - 100;

			window.scroll({
				top: scrollTo,
				behavior: 'smooth'
			});

		}, 100);

	}

	submitAddSlotTypeForm(form) {
		this.isSlotTypeSubmitted = false;

		if (this.isSlotTypeNew) {

			let details = {

				"lookupTypeId": 18, // vehcile category lookup id
				"lookupValueName": this.slot.slotMainType,
				"description": this.slot.slotType,
				"isActive": true,
				"insertedBy": parseInt(this.cookieService.get('userId')),
				"insertedOn": "2019-11-20T16:51:24.105Z",
				"updatedBy": 0,
				"updatedOn": "2019-11-20T16:51:24.105Z"

			}

			let params = {
				lookupvalue: details
			}

			this.lookupService.addLookupValue(params).subscribe((res: any) => {

				if (res.message) {
					this.isSlotTypeSubmitted = true;
					this.sharedService.setAlertMessage("Slot Type added successfully");
					this.isSlotTypeNew = !this.isSlotTypeNew;
					this.getSlotType();


				}
				else {

					this.isSlotTypeSubmitted = true;
					this.isError = true;
					this.alertMessage = res.errorMessage;
				}

			});

		} else {

			let details = {

				"lookupValueId": this.slotTypeUpdateId,
				"lookupTypeId": 18, // vehicle category lookup id
				"lookupValueName": this.slot.slotMainType,
				"description": this.slot.slotType,
				"isActive": true,
				"insertedBy": 0,
				"insertedOn": "2019-11-20T16:51:24.105Z",
				"updatedBy": parseInt(this.cookieService.get('userId')),
				"updatedOn": "2019-11-20T16:51:24.105Z"

			}

			let params = {
				lookupvalue: details
			}

			this.lookupService.updateLookupValue(params).subscribe((res: any) => {

				if (res.message) {
					this.isSlotTypeSubmitted = true;
					this.sharedService.setAlertMessage("Vehicle Type updated successfully");
					this.isSlotTypeUpdate = !this.isSlotTypeUpdate;
					this.getSlotType();



				}
				else {

					this.isSlotTypeSubmitted = true;
					this.isError = true;
					this.alertMessage = res.errorMessage;

				}

			});

		}
	}

	ngOnInit() {

		this.getSlotType();

		let slotMainParams = {
			LookupTypeId: 71
		}

		this.lookupService.getLookupValueByLookupTypeId(slotMainParams).subscribe((res: any) => {
			this.slotMainTypeDataList = res.filter(item => {
				return item.isActive;
			});

		});

	}

	/**
	 * Get Slot Type
	 */

	getSlotType() {

		let slotListParams = {
			LookupTypeId: 18
		}

		this.lookupService.getLookupValueByLookupTypeId(slotListParams).subscribe((res: any) => {

			this.slotDataList = res.filter(item => {
				return item.isActive;
			});

			this.totalItems = this.slotDataList.length;

			if (this.totalItems > this.itemLimit) {
				this.ItemEndIndex = this.itemLimit;
			}
			else {
				this.ItemEndIndex = this.totalItems;
			}

			this.isDataLoaded = true;
		});
	}

}
